* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  /* background: linear-gradient(to bottom, #f9d5e5, #ee9ca7); */
  background-color: #ee9ca7;
  background-repeat: repeat-y;
  background-size: 100% 9000px;
  color: black;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to bottom, #f68fbd, #ee9ca7);
  padding: 1em;
}

.cities {
  display: grid;
  padding: 2em ;
  margin-bottom: 2em;
  border: 2px solid black;
  text-align: start;
  justify-content:center;
}

.cities h2, h1{
  text-align: center;
  padding:0 10px 0 10px;
  margin:0 10px 10px 10px;
}

.date {
  margin-top: 1em;
  padding-top: 0;
  font-style: italic;
  text-align: center;
}

.city {
  margin-bottom: 20px;
}


.city-1,
.city-2,
.city-3,
.city-4,
.city-5,
.city-6 {
  margin: 4em 0 4em 0;
  width: 650px;
  height: 250px;
}

.city-img {
  width: 300px;
  height: 320px;
  float: left;
  margin-right: 2em;
}

a {
  text-decoration: none;
  font-size: .6em;
  color: rgb(91, 91, 88);
}

.description {
  font-size: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
}

a:hover,
a:focus {
  color: #ffd500;
}